.theme {
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 210px;
  right: 34px;
  width: 40px;
  height: 40px;
  background-color: #e5f4f3;
  border: 3px solid #000;
  border-radius: 50%;

  &__icon {
    width: 26px;
    height: 26px;
    fill: #000;
  }

  &__txt {
    left: 4px;
    color: $accent;
  }
}
